body {
    background-color: hsl(34, 85%, 51%)
  }
nav{
    background-color: #21995d;
    max-height: 120px;
    display: flex;
    align-items:center;
    justify-content:space-between; 
    position: sticky;
    top: 0;
    z-index: 100;
}
.navibar, .navibarRight{
    display: flex;
    align-items: center;
    margin: 0 15px;
}
.navibar ul li{
    list-style: none;
    display: inline-block;
    margin: 0 15px;
  
}
.logo{
    height: 120px;
    width: 120px;
    margin-right: 20px;
   
}
.search{
    width: 300px;
    padding: 0 15px;
    border-radius: 20px;
    display: inline-block;
    align-items: center;
    margin-left: 20px;
}
.title{
    font-style: italic;
    font-family: cursive;
    font-size: x-large;
   justify-self: center;
    align-self: center;
}
.container{
    display: flex;
    justify-content: space-between;
    padding:13px 5%;
}
.divLeft, .divRight{
    flex-basis: 25%;
    position: sticky;
    top: 70px;
    align-self: flex-start;
    height: 50vh;
}
.main{
    flex-basis: 47%;
}

.divLinks a{
    text-decoration: none;
    display: flex;
    align-items: center;
    width: fit-content;
    margin-bottom: 30px;

}